import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import CardHeader from "@mui/material/CardHeader";

const dispatchedEvents = [];

const SrsMuiCardHeader = function (props, ref) {
  let avatar = <slot name="avatar"/>;
  let action = <slot name="action"/>;
  return (
    <CardHeader
      title={props.title}
      subheader={props.subheader}
      avatar={avatar}
      action={action}
      disableTypography={props.disableTypography}
      theme={props.muiTheme}>
    </CardHeader>
  );
};

const styles = `
  /* Custom styles for the MuiCardHeader component */
`;

const props = [
  {
    name: "title",
    defaultValue: "",
    type: "string",
  },
  {
    name: "subheader",
    defaultValue: "",
    type: "string",
  },
  {
    name: "disableTypography",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiCardHeader),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
